import {
  VStack,
  Text,
  Slider,
  SliderTrack,
  SliderFilledTrack,
  SliderThumb,
} from "@chakra-ui/react";
import React, { useState } from "react";

const YearSelector = (props) => {
  const [year, setYear] = useState(2020);

  const handleYearChange = (val) => {
    setYear(val);
    props.onSelectYear(year);
  };

  return (
    <>
      <VStack width="100%" spacing={2}>
        <Text fontSize="2xl" fontWeight="semibold" color="#000">
          {year}
        </Text>
        <Slider
          width="80%"
          aria-label="yearSlider"
          defaultValue={2020}
          min={2017}
          max={2020}
          step={1}
          onChangeEnd={(val) => handleYearChange(val)}
        >
          <SliderTrack bg="#1D77b7">
            <SliderFilledTrack bg="#212b57" />
          </SliderTrack>
          <SliderThumb boxSize={4} bgColor="gray.200" />
        </Slider>
      </VStack>
    </>
  );
};

export default YearSelector;
