import React from "react";
import {
  BarChart,
  Bar,
  ResponsiveContainer,
  YAxis,
  XAxis,
  Tooltip,
  LabelList,
} from "recharts";
import { Box, Heading } from "@chakra-ui/react";
import CustomLabel from "./CustomLabel.js";

const formatFlowNumber = (num) => {
  var number = parseFloat(num.flow);
  var decimals = number.toFixed(2);

  return parseFloat(decimals);
};

const ExportPortGraph = (props) => {
  const flowData = props.flowData;

  if (!flowData) {
    return null;
  }

  const flows = flowData.filter(
    (flow) =>
      +flow.Year === props.year &&
      flow.Commodity === props.commodity &&
      flow.target === props.targetPort
  );

  flows.sort((a, b) => parseFloat(b.flow) - parseFloat(a.flow));

  flows.forEach((flow) => {
    flow.flow = formatFlowNumber(flow);
  });

  return (
    <>
      <Box height="100%" width="100%">
        <Heading size="sm">Top Origins</Heading>
        <ResponsiveContainer width="100%" height="90%">
          <BarChart
            data={flows.slice(0, 10)}
            layout="vertical"
            margin={{
              top: 10,
              right: 30,
              left: 80,
              bottom: 5,
            }}
          >
            <XAxis type="number" hide />
            <YAxis type="category" hide />
            <Bar dataKey="flow" fill="#38B2AC">
              <LabelList
                dataKey="flow"
                position="right"
                style={{
                  fontSize: "0.6em",
                  fill: "#000",
                }}
              />
              <LabelList
                dataKey="source"
                position="left"
                content={<CustomLabel />}
                style={{ fontSize: "0.6em" }}
              />
            </Bar>
            <Tooltip />
          </BarChart>
        </ResponsiveContainer>
      </Box>
    </>
  );
};

export default ExportPortGraph;
