import { Radio, RadioGroup, Stack } from "@chakra-ui/react";
import React, { useState } from "react";

const CommoditySelector = (props) => {
  const [commodity, setCommodity] = useState("Thermal");

  const handleSelection = (val) => {
    setCommodity(val);

    props.onSelectCommodity(val);
  };

  return (
    <RadioGroup onChange={handleSelection} value={commodity}>
      <Stack direction="row" color="gray.600">
        <Radio value="Total">Total</Radio>
        <Radio value="Thermal">Thermal</Radio>
        <Radio value="Metallurgical">Metallurgical</Radio>
      </Stack>
    </RadioGroup>
  );
};

export default CommoditySelector;
