import { Text } from "recharts";

const CustomLabel = (props) => {
  return (
    <Text
      fontSize="0.6em"
      x={props.x - 5}
      y={props.y + props.height / 2}
      width={95}
      textAnchor="end"
      verticalAnchor="middle"
      dominantBaseline="middle"
    >
      {props.value}
    </Text>
  );
};

export default CustomLabel;
