import { Radio, RadioGroup, Stack } from "@chakra-ui/react";
import React, { useState } from "react";

const LevelSelector = (props) => {
  const [level, setLevel] = useState("Ports");

  const handleSelection = (val) => {
    setLevel(val);

    props.onSelectLevel(val);
  };

  return (
    <RadioGroup onChange={handleSelection} value={level}>
      <Stack direction="row" color="gray.600">
        <Radio value="Countries">Country</Radio>
        <Radio value="Ports">Ports</Radio>
      </Stack>
    </RadioGroup>
  );
};

export default LevelSelector;
