import { Center, Stat, StatLabel, StatNumber } from "@chakra-ui/react";

const CO2Display = (props) => {
  const CO2Amount = Math.abs((+props.mtAmount * 2.86).toFixed(2));

  return (
    <Center>
      <Stat>
        <StatLabel color="gray.600">CO2 emissions if coal burned:</StatLabel>
        <StatNumber>{`${CO2Amount} Mt CO2e`}</StatNumber>
      </Stat>
    </Center>
  );
};

export default CO2Display;
