import { PieChart, Pie, Cell, Legend } from "recharts";
import { Heading, Box } from "@chakra-ui/react";

import "../styles/legend.css";

const COLORS = ["gray", "brown", "black"];

const PortInfo = (props) => {
  const portData = props.portData;

  if (!portData) {
    return null;
  }

  const filterData = portData.filter(
    (port) => +port.Year === props.year && port.Port === props.selectedPort
  );

  const data = filterData.map((obj) => ({ ...obj }));

  data.forEach((port) => {
    port.flow = Math.abs(+port.flow);
    port.Commodity =
      port.Commodity === "Total" ? "Unspecified" : port.Commodity;
  });

  try {
    data[2].flow = data[2].flow - (data[0].flow + data[1].flow);
  } catch (error) {}

  console.log(data);

  return (
    <>
      <Box
        height="100%"
        width="100%"
        justifyContent="center"
        alignItems="center"
      >
        <Heading size="sm"> Commodity breakdown</Heading>
        <PieChart height={130} width={180}>
          <Pie
            cx={90}
            cy={55}
            data={data}
            innerRadius={20}
            outerRadius={50}
            dataKey="flow"
            nameKey="Commodity"
          >
            {data.map((entry, index) => (
              <Cell
                key={`cell-${index}`}
                fill={COLORS[index % COLORS.length]}
              />
            ))}
          </Pie>
        </PieChart>
        <ul className="legend">
          <li>
            <span className="Metallurgical-legend-item"></span> Metallurgical
            coal
          </li>
          <li>
            <span className="Thermal-legend-item"></span> Thermal coal
          </li>
          <li>
            <span className="Unspecified-legend-item"></span> Unspecified coal
          </li>
        </ul>
      </Box>
    </>
  );
};

export default PortInfo;
