import React from "react";
import { useMines } from "../loaders/useMines";
import {
  BarChart,
  Bar,
  ResponsiveContainer,
  YAxis,
  XAxis,
  Tooltip,
  LabelList,
} from "recharts";
import { Heading, Box } from "@chakra-ui/react";
import CustomLabel from "./CustomLabel.js";

import "../styles/legendItem.css";

const getDistance = (lat1, lon1, lat2, lon2) => {
  var R = 6371; // Radius of the earth in km
  var dLat = deg2rad(lat2 - lat1); // deg2rad below
  var dLon = deg2rad(lon2 - lon1);
  var a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(deg2rad(lat1)) *
      Math.cos(deg2rad(lat2)) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);
  var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  var d = R * c; // Distance in km
  return d;
};

const deg2rad = (deg) => {
  return deg * (Math.PI / 180);
};

const MineDisplay = (props) => {
  const mines = useMines();

  if (!mines) {
    return <pre>Loading...</pre>;
  }

  const port = props.sourcePort;

  mines.forEach((mine) => {
    mine.Distance = getDistance(
      port.lat,
      port.lon,
      mine.Latitude,
      mine.Longitude
    );

    mine.Output = parseFloat(mine.Output);
  });

  mines.sort((a, b) => parseFloat(a.Distance) - parseFloat(b.Distance));

  if (props.commodity === "Thermal") {
    var commodityMines = mines.filter((mine) => mine.Thermal === "TRUE");
  } else if (props.commodity === "Metallurgical") {
    commodityMines = mines.filter((mine) => mine.Metallurgical === "TRUE");
  } else {
    commodityMines = mines;
  }

  const topMines = commodityMines.slice(0, 10);

  return (
    <>
      <Box height="100%" width="100%">
        <Heading size="sm">Closest mines</Heading>
        <ResponsiveContainer width="100%" height="90%">
          <BarChart
            data={topMines}
            layout="vertical"
            margin={{
              top: 10,
              right: 30,
              left: 100,
              bottom: 5,
            }}
          >
            <XAxis type="number" hide />
            <YAxis type="category" hide />
            <Bar dataKey="Output" fill="tomato">
              <LabelList
                dataKey="Output"
                position="right"
                style={{
                  fontSize: "0.6em",
                  fill: "#000",
                }}
              />
              <LabelList
                dataKey="Name"
                position="left"
                content={<CustomLabel />}
                style={{ fontSize: "0.6em" }}
              />
            </Bar>
            <Tooltip />
          </BarChart>
        </ResponsiveContainer>
      </Box>
    </>
  );
};

export default MineDisplay;
