import React, { useEffect, useState } from "react";
import { MapContainer, TileLayer, useMap } from "react-leaflet";
import L from "leaflet";
import * as d3 from "d3";
import "leaflet/dist/leaflet.css";

import { usePorts } from "../loaders/usePorts";
import { useFlows } from "../loaders/useFlows";
import { useCountries } from "../loaders/useCountries";
import { useCountryFlows } from "../loaders/useCountryFlows";
import { pointer } from "d3";
import { Spinner } from "@chakra-ui/react";

import "../styles/portTooltip.css";

const Map = (props) => {
  const [tooltipPosition, setTooltipPosition] = useState([0, 0]);

  const loadPorts = usePorts();
  const loadFlows = useFlows();
  const loadCountries = useCountries();
  const loadCountryFlows = useCountryFlows();

  if (!loadPorts || !loadFlows || !loadCountries || !loadCountryFlows) {
    return (
      <pre>
        <Spinner color="#38B2AC" />
      </pre>
    );
  }

  var allPorts = loadPorts;
  var allFlows = loadFlows;
  var countryNodes = loadCountries;
  var countryFlows = loadCountryFlows;

  if (props.levelSelected !== "Ports") {
    allPorts = countryNodes;
    allFlows = countryFlows;
  }

  const D3Layer = () => {
    const map = useMap();

    d3.select(map.getPanes().overlayPane).selectAll("*").remove();

    L.svg({ clickable: true }).addTo(map);

    useEffect(() => {
      var flows = allFlows.filter(
        (flows) =>
          +flows.Year === props.yearSelected &&
          flows.Commodity === props.commoditySelected
      );

      var ports = allPorts.filter(
        (port) =>
          +port.Year === props.yearSelected &&
          port.Commodity === props.commoditySelected
      );

      props.setPortData(allPorts);
      props.setFlowData(allFlows);

      const overlay = d3.select(map.getPanes().overlayPane);

      const svg = overlay.select("svg").attr("pointer-events", "auto");

      const linkLayer = svg.append("g");
      const portLayer = svg.append("g");

      const portTooltip = overlay
        .append("div")
        .attr("id", "portTooltip")
        .style("left", tooltipPosition[0] + 30 + "px")
        .style("top", tooltipPosition[1] + 30 + "px");

      function mouseover(event, d) {
        d3.select(this).attr("fill-opacity", 1).style("cursor", "pointer");

        var flowNum = +d.flow;
        var displayFlow =
          flowNum > 0 ? flowNum.toFixed(2) : -flowNum.toFixed(2);

        var importExport = +d.flow > 0 ? "Export" : "Import";

        portTooltip
          .html(
            `<p style="font-size: 1.5em"><b>${d.Port}</b></p>
            <p>${importExport}: ${displayFlow} megatonnes</p>
            <p><b>Click</b> to see more details.</p>`
          )
          .style("opacity", 1);

        const sourcePort = d.Port;

        if (d.flow > 0) {
          var flowData = flows.filter((flow) => flow.source === sourcePort);
        } else {
          flowData = flows.filter((flow) => flow.target === sourcePort);
        }

        function pathDraw(sourceCoords, targetCoords) {
          var source = map.latLngToLayerPoint(sourceCoords),
            target = map.latLngToLayerPoint(targetCoords),
            dx = source.x - target.x,
            dy = source.y - target.y;

          var sx = 0.4,
            sy = 0.1;

          if (dy < 0) {
            var controls = [sx * dx, sy * dy, sx * dx, sy * dy];
          } else {
            controls = [sy * dx, sx * dy, sy * dx, sx * dy];
          }

          return (
            "M" +
            source.x +
            "," +
            source.y +
            "C" +
            (source.x - controls[0]) +
            "," +
            (source.y - controls[1]) +
            " " +
            (target.x + controls[2]) +
            "," +
            (target.y + controls[3]) +
            " " +
            target.x +
            "," +
            target.y
          );
        }

        var Links = linkLayer.selectAll("path").data(flowData);

        Links.enter()
          .append("path")
          .attr("class", "links")
          .attr("id", (d) => {
            return d.flow > 0 ? d.target : d.source;
          })
          .attr("d", (d) => {
            var sourcePort = allPorts.filter((port) => port.Port === d.source);
            var targetPort = allPorts.filter((port) => port.Port === d.target);
            var sourceCoords = [sourcePort[0].lat, sourcePort[0].lon];
            var targetCoords = [targetPort[0].lat, targetPort[0].lon];

            return pathDraw(sourceCoords, targetCoords);
          })
          .style("fill", "none")
          .style("stroke", (d) => {
            return importExport === "Export" ? "tomato" : "#38B2AC";
          })
          .style("stroke-width", (d) => {
            var width = +d.flow > 0 ? d.flow : -d.flow;
            if (props.levelSelected === "Ports") {
              return width + "px";
            } else {
              return width / 2 + "px";
            }
          })
          .style("opacity", 0.6);

        Links.exit().remove();
      }

      function mouseleave(event, d) {
        d3.select(this)
          .transition()
          .duration("150")
          .attr("fill-opacity", 0.4)
          .style("cursor", "default");

        portTooltip
          .style("opacity", 0)
          .style("left", 0 + "px")
          .style("top", 0 + "px");

        linkLayer.selectAll("path").remove();
      }

      function mousemove(event, d) {
        portTooltip
          .style("left", pointer(event)[0] + 30 + "px")
          .style("top", pointer(event)[1] + 30 + "px");
      }

      function click(event, d) {
        setTooltipPosition([pointer(event)[0], pointer(event)[1]]);

        props.onSelectPort(d);
        console.log(d);
      }

      var Dots = portLayer
        .selectAll("circle")
        .attr("class", "Dots")
        .data(ports)
        .join("circle")
        .attr("id", "Ports")
        .attr("fill", (d) => (+d["flow"] < 0 ? "#38B2AC" : "tomato"))
        .attr("stroke", "black")
        .attr("fill-opacity", 0.4)
        .attr("cx", (d) => {
          return map.latLngToLayerPoint([d.lat, d.lon]).x;
        })
        .attr("cy", (d) => map.latLngToLayerPoint([d.lat, d.lon]).y)
        .attr("r", (d) => {
          var radius = +d.flow > 0 ? d.flow : -d.flow;

          if (props.levelSelected === "Ports") {
            return Math.sqrt(20 * +radius);
          } else {
            return Math.sqrt(10 * +radius);
          }
        })
        .on("mouseover", mouseover)
        .on("mouseleave", mouseleave)
        .on("click", click)
        .on("mousemove", mousemove);

      const update = () =>
        Dots.attr("cx", (d) => map.latLngToLayerPoint([d.lat, d.lon]).x).attr(
          "cy",
          (d) => map.latLngToLayerPoint([d.lat, d.lon]).y
        );

      map.on("zoomend", update);
    }, [map]);

    return null;
  };

  return (
    <>
      <MapContainer
        id="mainMap"
        style={{ height: "100%" }}
        center={[5, 18]}
        zoom={2}
        scrollWheelZoom={true}
      >
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
          url="https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png"
        />

        <D3Layer />
      </MapContainer>
    </>
  );
};

export default Map;
