import React, { useState } from "react";
import "./App.css";
import Map from "./components/Map";
import "./styles/custom.css";
import "./styles/outerContainer.css";
import { Grid, GridItem, Box, Center, Divider, VStack } from "@chakra-ui/react";
import PortLinkSidebar from "./components/PortLinkSidebar";
import YearSelector from "./components/YearSelector";
import CommoditySelector from "./components/CommoditySelector";
import MtDisplay from "./components/MtDisplay";
import CO2Display from "./components/CO2Display";
import LevelSelector from "./components/LevelSelector";
import PortDisplay from "./components/PortDisplay";
import MinePlantSidebar from "./components/MinePlantSidebar";
import Tour from "reactour";

const steps = [
  {
    selector: ".css-4n55e8",
    content:
      "You can see your selected port and country and whether it is an export or import installation up here.",
  },
  {
    selector: ".css-3dj3vp",
    content:
      "Hovering over a port or country circle shows its trade flows with other ports or countries. Select a port or country by clicking on the circle.",
  },
  {
    selector: ".css-1gt0haz",
    content:
      "When a port or country is selected, this panel shows connected ports and countries as well as a breakdown of the coal traded here.",
  },
  {
    selector: ".css-1w8f165",
    content:
      "Use these controls to select year, commodity and level (country or port). The map shows coal trade flows based on your selection.",
  },
  {
    selector: ".css-bxsqib",
    content:
      "When a port or country is selected, this panel shows the closest mines for export installations and closest coal power plants for import installations.",
  },
];

function App() {
  const [isTourOpen, setIsTourOpen] = useState(true);
  const [clickedPort, setClickedPort] = useState({
    Commodity: "Thermal",
    Port: "Newcastle",
    Year: "2020",
    flow: "130.296205",
    lat: "-32.88979846221539",
    lon: "151.76871572604225",
  });
  const [yearSelected, setYear] = useState(2020);
  const [commoditySelected, setCommodity] = useState("Thermal");
  const [levelSelected, setLevel] = useState("Ports");
  const [portData, setPortData] = useState(null);
  const [flowData, setFlowData] = useState(null);

  return (
    <>
      <div className="outerContainer">
        <Grid
          maxHeight="900px"
          maxWidth="1600px"
          w="100vw"
          h="100vh"
          templateRows="repeat(20, 1fr)"
          templateColumns="repeat(5, 1fr)"
          gap="4"
        >
          <GridItem colStart={1} colEnd={1} rowStart={1} rowEnd={4}>
            <Box
              width="100%"
              height="100%"
              bg="white"
              borderRadius="lg"
              borderWidth="5pt"
              borderColor={clickedPort.flow > 0 ? "tomato" : "#38B2AC"}
              color="black"
              shadow="2xl"
              alignItems="center"
              justifyContent="center"
              display="flex"
            >
              <PortDisplay clickedPort={clickedPort} />
            </Box>
          </GridItem>
          <GridItem colStart={1} colEnd={1} rowStart={4} rowEnd={21}>
            <Box
              width="100%"
              height="100%"
              bg="white"
              borderRadius="lg"
              color="black"
              shadow="2xl"
              alignItems="center"
              justifyContent="center"
              padding="5"
              display="flex"
            >
              <PortLinkSidebar
                clickedPort={clickedPort}
                portData={portData}
                flowData={flowData}
                yearSelected={yearSelected}
                commoditySelected={commoditySelected}
                levelSelected={levelSelected}
              />
            </Box>
          </GridItem>
          <GridItem rowSpan={3} colSpan={1}>
            <Box
              width="100%"
              height="100%"
              bg="white"
              borderRadius="lg"
              color="black"
              shadow="2xl"
              alignItems="center"
              justifyContent="center"
              display="flex"
            >
              <MtDisplay mtAmount={clickedPort.flow} />
            </Box>
          </GridItem>
          <GridItem rowSpan={3} colSpan={1}>
            <Box
              width="100%"
              height="100%"
              bg="white"
              borderRadius="lg"
              color="black"
              shadow="2xl"
              alignItems="center"
              justifyContent="center"
              display="flex"
            >
              <CO2Display mtAmount={clickedPort.flow} />
            </Box>
          </GridItem>
          <GridItem rowSpan={3} colSpan={2}>
            <Box
              width="100%"
              height="100%"
              bg="white"
              borderRadius="lg"
              color="black"
              shadow="2xl"
              alignItems="center"
              overflow="hidden"
              display="flex"
            >
              <Box
                width="40%"
                height="100%"
                alignItems="center"
                justifyContent="center"
                display="flex"
              >
                <YearSelector onSelectYear={(value) => setYear(value)} />
              </Box>
              <Center height="50px">
                <Divider orientation="vertical" />
              </Center>
              <Box
                width="60%"
                height="100%"
                alignItems="center"
                justifyContent="center"
                display="flex"
              >
                <VStack spacing={2}>
                  <CommoditySelector
                    onSelectCommodity={(value) => setCommodity(value)}
                  />
                  <Divider orientation="horizontal" />
                  <LevelSelector onSelectLevel={(value) => setLevel(value)} />
                </VStack>
              </Box>
            </Box>
          </GridItem>
          <GridItem rowSpan={17} colSpan={3}>
            <Box
              width="100%"
              height="100%"
              bg="white"
              borderRadius="2xl"
              color="black"
              shadow="2xl"
              alignItems="center"
              overflow="hidden"
            >
              <Map
                onSelectPort={(object) => setClickedPort(object)}
                yearSelected={yearSelected}
                commoditySelected={commoditySelected}
                levelSelected={levelSelected}
                setPortData={(object) => setPortData(object)}
                setFlowData={(object) => setFlowData(object)}
              />
            </Box>
          </GridItem>
          <GridItem colStart={5} colEnd={5} rowStart={4} rowEnd={21}>
            <Box
              width="100%"
              height="100%"
              bg="white"
              borderRadius="lg"
              color="black"
              shadow="2xl"
              alignItems="center"
              justifyContent="center"
              padding="5"
              display="flex"
            >
              <MinePlantSidebar
                clickedPort={clickedPort}
                portData={portData}
                flowData={flowData}
                yearSelected={yearSelected}
                commoditySelected={commoditySelected}
                levelSelected={levelSelected}
              />
            </Box>
          </GridItem>
        </Grid>
        <Tour
          steps={steps}
          isOpen={isTourOpen}
          onRequestClose={() => setIsTourOpen(false)}
        />
      </div>
    </>
  );
}

export default App;
