import React from "react";
import { VStack, Box } from "@chakra-ui/react";
import ExportPortGraph from "./ExportPortGraph.js";
import ImportPortGraph from "./ImportPortGraph.js";
import PortInfo from "./PortInfo.js";

const PortLinkSidebar = (props) => {
  const selectedPort = props.clickedPort;
  const portName = selectedPort.Port;

  if (+selectedPort.flow > 0) {
    var flowDirection = "Export";
  } else {
    flowDirection = "Import";
  }

  if (flowDirection === "Export") {
    return (
      <>
        <VStack height="100%" width="100%" spacing="20px">
          <Box height="calc(50% - 10px)" width="100%">
            <ExportPortGraph
              sourcePort={portName}
              flowData={props.flowData}
              portData={props.portData}
              year={props.yearSelected}
              level={props.levelSelected}
              commodity={props.commoditySelected}
            />
          </Box>
          <Box height="calc(50% - 10px)" width="100%">
            <PortInfo
              selectedPort={portName}
              portData={props.portData}
              year={props.yearSelected}
            />
          </Box>
        </VStack>
      </>
    );
  } else {
    return (
      <>
        <VStack height="100%" width="100%" spacing="20px">
          <Box height="calc(50% - 10px)" width="100%">
            <ImportPortGraph
              targetPort={portName}
              flowData={props.flowData}
              portData={props.portData}
              year={props.yearSelected}
              level={props.levelSelected}
              commodity={props.commoditySelected}
            />
          </Box>
          <Box height="calc(50% - 10px)" width="100%">
            <PortInfo
              selectedPort={portName}
              portData={props.portData}
              year={props.yearSelected}
            />
          </Box>
        </VStack>
      </>
    );
  }
};

export default PortLinkSidebar;
